import { ref } from 'vue'

const getPost = (id) => {
    const post = ref(null)
    const error = ref(null)

    const load = async () => {
      try{
        let data = await fetch('https://my-json-server.typicode.com/farhanade40/api/posts/' + id) 
        if(!data.ok){
          throw Error('no data available')
        }
        post.value = await data.json()
      }
      catch (err) {
        error.value = err.message
        console.log(error.value)
      }
    }
        return { post, error, load}
}
 
export default getPost